module.exports = ":host{display:contents;flex-direction:column;font-family:Unifont;font-size:16px}#toolbar{width:calc(100% - 32px);height:32px;background-color:hsla(0,0%,100%,.8);backdrop-filter:blur(10px);border:2px solid #000;border-top:none;border-bottom:none;box-sizing:border-box;padding:0 16px;margin:0 auto 16px;border-radius:8px;display:flex;align-items:center}.padding{flex:1}#empty{background-color:hsla(0,0%,100%,.2);backdrop-filter:blur(10px);border:2px solid #000;border-top:none;border-bottom:none;width:200px;position:relative;height:150px;margin:auto;border-radius:8px}#empty>icon-unavailable,#empty>span{position:absolute;top:50%;left:50%;transform:translate(-50%,-50%)}#empty>span{width:fit-content}#result{background-color:hsla(0,0%,100%,.2);backdrop-filter:blur(10px);border:2px solid #000;border-top:none;border-bottom:none;width:200px;margin:auto;border-radius:8px;list-style:none;padding:0}#result>li{list-style:none;margin:0;padding:4px 8px}terminal-manager-content-renderer{display:flex;flex-direction:column;height:100%}#error{background-color:hsla(0,0%,100%,.2);backdrop-filter:blur(10px);border:2px solid red;border-top:none;border-bottom:none;width:200px;position:relative;height:150px;margin:auto;border-radius:8px;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}#error button{background-color:#fff;font-family:Unifont;font-size:16px;padding:2px 8px;border-radius:100px;border:2px solid #000}:focus{outline:none}"